import { fetch } from './base';
export default {
  sendCode(req) {
    return fetch('post', '/api/dq/sx/sendCode', req, { 'Content-Type': 'multipart/form-data' });
  },
  saveOrders(req) {
    return fetch('post', '/api/dq/sx/saveOrders', req, { 'Content-Type': 'multipart/form-data' })
  },
};


